body { font-family: sans-serif; }

 
.container-layer {
/*  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: url(../images/gradient.png) no-repeat center;
  background-size: cover;
  overflow: hidden;
  width: 100%;
  height: 100%;
  z-index: 9;
  visibility: hidden;*/

  background: #000;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 8;
  top:0;
  visibility: hidden;
}

.container-layer.active { 
  visibility: visible;
  //animation: bgchange 1s forwards;
}
.container-layer.active .close-zoom-btn, .container-layer.active .carousel { 
  opacity:1;
  transition: opacity 1s ease-in-out 1s;
}
.carousel {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 5.1rem;

  opacity: 0;
  transition: opacity 0s; 
}

.carousel-image {
  display: block;
  height: 29.5rem;
  /* set min-width, allow images to set cell width */
  min-width: 13.6rem;
  max-width: 100%;
  margin-right: 5rem;
  /* vertically center */
  top: 50%;
  transform: translateY(-50%);
}

.carousel.is-fullscreen .carousel-image {
  height: auto;
  max-height: 100%;
}

.flickity-prev-next-button .arrow {
  fill: white;
}
.flickity-prev-next-button {
  top: 35%;
}

.flickity-button {
  background: transparent;
  border: 0.1rem solid white;
  filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.8));
}
.flickity-button:hover {
  background: transparent;
}

